const util = require("./util.js");

function enterWithDic(dic) {
  console.log(dic.product_type);

  if (dic.appid) {
    wx.navigateToMiniProgram({
      appId: dic.appid,
      envVersion: 'release',

      success(res) {// 
      }

    });
  } else {
    var url = encodeURIComponent(dic.url);
    console.log('dic', dic);

    switch (dic.product_type) {
      case '2':
        //
        wx.navigateTo({
          url: '../course/course?url=' + url
        });
        break;

      case '3':
        // 
        wx.navigateTo({
          url: '../courseDetail/courseDetail?url=' + url
        });
        break;

      case '22':
        //
        wx.navigateTo({
          url: '../myVip/myVip?url=' + url
        });
        break;

      case '30':
        // 
        wx.navigateTo({
          url: '../order/myOrder'
        });
        break;

      case '32':
        //
        wx.navigateTo({
          url: '../login/bindingPhone'
        });
        break;

      case '34':
        //
        wx.navigateTo({
          url: '../login/changePhone'
        });
        break;

      case '231':
        //
        if (!util.isLogin()) {
          wx.navigateTo({
            url: '../login/login'
          });
        } else {
          wx.navigateTo({
            url: '../livevc/livevc?url=' + url
          });
        }

        break;

      case '239':
        //
        if (!util.isLogin()) {
          wx.navigateTo({
            url: '../login/login'
          });
        } else {
          wx.navigateTo({
            url: '../livevc/vodvc?url=' + url
          });
        }

        break;

      case '310':
        // 
        var system = wx.getSystemInfoSync().system;

        if (system.indexOf('iOS') >= 0) {
          wx.showModal({
            title: 'APP',
            content: 'APP',
            showCancel: false,
            confirmText: ''
          });
        } else {
          wx.navigateTo({
            url: '../order/order?url=' + url
          });
        }

        break;

      case '9100':
        //
        wx.navigateTo({
          url: '../myCourse/myCourse?url=' + url
        });
        break;

      case '9998':
        //(,)
        wx.navigateTo({
          url: '../audio/audio'
        });
        break;

      case '9999':
        wx.navigateTo({
          url: '../webview/webview?url=' + url
        });
        break;

      default:
        let urls = decodeURIComponent(url);

        if (urls.indexOf('isrank') !== -1) {
          console.log('is test count', urls, getQueryString(urls, 'id'));
          util.updateHot(wx.getStorageSync('userinfo')['uid'] || 0, getQueryString(urls, 'id'), 0, 4);
        }

        wx.navigateTo({
          url: urls
        });
        break;
    }
  }
}

function getQueryString(url, name) {
  var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
  var r = url.substr(1).match(reg); //search,

  if (r != null) return unescape(r[2]);
  return null;
}

module.exports = {
  enterWithDic: enterWithDic
};